<template>
	<view class="page">
		<!-- ÊêúÁ¥¢Ê†è -->
		<view class="search-bar">
			<view class="search-input-wrapper">
				<text class="search-icon">üîç</text>
				<input 
					class="search-input" 
					v-model="searchKeyword"
					placeholder="ÊêúÁ¥¢ËçØÊùêÂêçÁß∞/‰ª£Á†Å"
					@input="onSearchInput"
					confirm-type="search"
				/>
				<text v-if="searchKeyword" class="clear-icon" @click="clearSearch">‚úï</text>
			</view>
		</view>

		<!-- Á≠õÈÄâÊù°‰ª∂ -->
		<view class="filter-bar">
			<view class="filter-item" @click="showCategoryPicker = true">
				<text>{{ selectedCategory || 'ÂÖ®ÈÉ®ÂàÜÁ±ª' }}</text>
				<text class="arrow">‚Ä∫</text>
			</view>
		</view>

		<!-- ËçØÊùêÂàóË°®ÔºàÁÇπÂáªÁõ¥Êé•ÊòæÁ§∫ÊâπÊ¨°Ôºâ -->
		<view class="drug-list">
			<view 
				v-for="drug in filteredDrugList" 
				:key="drug._id" 
				class="drug-card"
				@tap="selectDrug(drug)"
			>
				<!-- ËçØÊùê‰∏ªË¶Å‰ø°ÊÅØ -->
				<view class="drug-main">
					<view class="drug-header">
						<text class="drug-name">{{ drug.name }}</text>
						<view class="drug-badges">
							<text v-if="drug.isHighValue" class="badge high-value">È´òÂÄº</text>
							<text v-if="drug.isEmergency" class="badge emergency">ÊÄ•Êïë</text>
						</view>
					</view>
					
					<view class="drug-details">
						<view class="detail-row">
							<text class="detail-label">ËßÑÊ†ºÔºö</text>
							<text class="detail-value">{{ drug.specification }}</text>
						</view>
						<view class="detail-row">
							<text class="detail-label">Âçï‰ΩçÔºö</text>
							<text class="detail-value">{{ drug.unit }}</text>
						</view>
						<view v-if="drug.drugCode" class="detail-row">
							<text class="detail-label">‰ª£Á†ÅÔºö</text>
							<text class="detail-value">{{ drug.drugCode }}</text>
						</view>
						<view v-if="drug.manufacturer" class="detail-row">
							<text class="detail-label">ÂéÇÂÆ∂Ôºö</text>
							<text class="detail-value">{{ drug.manufacturer }}</text>
						</view>
					</view>
				</view>
				
				<!-- Âè≥‰æßÊìç‰ΩúÊèêÁ§∫ -->
				<view class="drug-action">
					<view class="action-icon">üì¶</view>
					<text class="action-text">ÈÄâÊã©ÊâπÊ¨°</text>
					<text class="action-arrow">‚Ä∫</text>
				</view>
			</view>

			<!-- Á©∫Áä∂ÊÄÅ -->
			<view v-if="filteredDrugList.length === 0" class="empty-state">
				<text class="empty-icon">üì¶</text>
				<text class="empty-text">{{ searchKeyword ? 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥ËçØÊùê' : 'ÊöÇÊó†ËçØÊùêÊï∞ÊçÆ' }}</text>
			</view>

			<!-- Âä†ËΩΩÁä∂ÊÄÅ -->
			<view v-if="loading" class="loading-state">
				<text>Âä†ËΩΩ‰∏≠...</text>
			</view>
		</view>

			<!-- ÊâπÊ¨°ÈÄâÊã©ÂºπÁ™ó -->
		<view v-if="showBatchSelector" class="batch-popup-mask" @tap="closeBatchSelector">
			<view class="batch-popup" @tap.stop>
				<view class="popup-header">
					<text class="popup-title">ÈÄâÊã©ÊâπÊ¨°</text>
					<view class="close-btn" @tap="closeBatchSelector">‚úï</view>
				</view>
				<view v-if="selectedDrug" class="drug-info-bar">
					<text>ËçØÊùêÔºö{{ selectedDrug.name }}</text>
					<text>ËßÑÊ†ºÔºö{{ selectedDrug.specification }}</text>
				</view>
				<view class="batch-list-container">
					<view v-if="loading" class="loading-hint">Âä†ËΩΩ‰∏≠...</view>
					<view v-else-if="batchList.length === 0" class="empty-hint">
						<text>üì¶</text>
						<text>ËØ•ËçØÊùêÊöÇÊó†Â∫ìÂ≠ò</text>
					</view>
					<view v-else>
						<view 
							v-for="batch in batchList" 
							:key="batch._id"
							class="batch-item"
							@tap="selectBatch(batch)"
						>
							<view class="batch-header">
								<text class="batch-number">ÊâπÂè∑Ôºö{{ batch.batch }}</text>
								<text class="stock-quantity">Â∫ìÂ≠òÔºö{{ batch.quantity }} {{ selectedDrug.unit }}</text>
							</view>
							<view class="batch-details">
								<text>Áîü‰∫ßÊó•ÊúüÔºö{{ batch.productionDate }}</text>
								<text>ÊúâÊïàÊúüËá≥Ôºö{{ batch.expireDate }}</text>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>

		<!-- ÂàÜÁ±ªÈÄâÊã©Âô® -->
		<picker 
			v-if="showCategoryPicker"
			:value="categoryIndex"
			:range="categories"
			@change="onCategoryChange"
			@cancel="showCategoryPicker = false"
		>
		</picker>
	</view>
</template>

<script>
export default {
	data() {
		return {
			loading: false,
			drugList: [],
			filteredDrugList: [],
			searchKeyword: '',
			categories: ['ÂÖ®ÈÉ®ÂàÜÁ±ª', 'Ë•øËçØ', '‰∏≠ÊàêËçØ', '‰∏≠ËçØÈ•ÆÁâá', 'ÂåªÁñóÂô®Ê¢∞'],
			categoryIndex: 0,
			selectedCategory: '',
			showCategoryPicker: false,
			// Êîπ‰∏∫ÂçïÈÄâÊ®°ÂºèÔºåÈÄâ‰∏≠ÂêéÁ´ãÂç≥ÊòæÁ§∫ÊâπÊ¨°
			selectedDrug: null,
			showBatchSelector: false,
			batchList: []
		}
	},
	
	onLoad() {
		this.loadDrugList()
	},
	
	methods: {
		async loadDrugList() {
			this.loading = true
			try {
				// ‚≠ê ÂÖ≥ÈîÆ‰øÆÂ§çÔºöÊü•ËØ¢ËçØÂ∫ìÔºàdrug_storageÔºâ‰∏≠ÊâÄÊúâÊúâÂ∫ìÂ≠òÁöÑËçØÊùê
				const db = wx.cloud.database()
				const stockResult = await db.collection('stock')
					.where({
						location: 'drug_storage',  // ‚≠ê Âè™Êü•ËØ¢ËçØÂ∫ìÁöÑÂ∫ìÂ≠ò
						quantity: db.command.gt(0)  // Â∫ìÂ≠òÂ§ß‰∫é0
					})
					.get()
				
				console.log('üì¶ ËçØÂ∫ìÂ∫ìÂ≠òÊü•ËØ¢ÁªìÊûú:', stockResult.data.length, 'Êù°ËÆ∞ÂΩï')
				
				if (!stockResult.data || stockResult.data.length === 0) {
					this.drugList = []
					this.filteredDrugList = []
					uni.showToast({
						title: 'ËçØÂ∫ìÊöÇÊó†Â∫ìÂ≠ò',
						icon: 'none'
					})
					return
				}
				
				// Ëé∑ÂèñÊúâÂ∫ìÂ≠òÁöÑËçØÊùêIDÂàóË°®ÔºàÂéªÈáçÔºâ
				const drugIds = [...new Set(stockResult.data.map(item => item.drugId))]
				console.log('üìã ÊúâÂ∫ìÂ≠òÁöÑËçØÊùêIDÊï∞Èáè:', drugIds.length)
				
				// Êü•ËØ¢Ëøô‰∫õËçØÊùêÁöÑËØ¶ÁªÜ‰ø°ÊÅØ
				const result = await this.$api.callFunction('getDrugList', {
					keyword: '',
					category: 'all',
					page: 1,
					pageSize: 1000
				})
				
				if (result.success) {
					// Âè™‰øùÁïôÊúâÂ∫ìÂ≠òÁöÑËçØÊùê
					this.drugList = (result.data || []).filter(drug => 
						drugIds.includes(drug._id)
					)
					this.filteredDrugList = this.drugList
					
					console.log(`‚úÖ Âä†ËΩΩ‰∫Ü ${this.drugList.length} ÁßçËçØÂ∫ìÊúâÂ∫ìÂ≠òÁöÑËçØÊùê`)
					
					if (this.drugList.length === 0) {
						uni.showToast({
							title: 'ËçØÂ∫ìÊöÇÊó†ÂèØÂá∫Â∫ìËçØÊùê',
							icon: 'none'
						})
					}
				} else {
					throw new Error(result.message || 'Âä†ËΩΩÂ§±Ë¥•')
				}
			} catch (err) {
				console.error('‚ùå Âä†ËΩΩËçØÊùêÂàóË°®Â§±Ë¥•:', err)
				uni.showToast({
					title: err.message || 'Âä†ËΩΩÂ§±Ë¥•',
					icon: 'none'
				})
			} finally {
				this.loading = false
			}
		},
		
		onSearchInput() {
			this.filterDrugs()
		},
		
		clearSearch() {
			this.searchKeyword = ''
			this.filterDrugs()
		},
		
		filterDrugs() {
			let list = this.drugList
			
			// ÊåâÂàÜÁ±ªÁ≠õÈÄâ
			if (this.selectedCategory && this.selectedCategory !== 'ÂÖ®ÈÉ®ÂàÜÁ±ª') {
				list = list.filter(drug => drug.category === this.selectedCategory)
			}
			
			// ÊåâÂÖ≥ÈîÆËØçÁ≠õÈÄâ
			if (this.searchKeyword) {
				const keyword = this.searchKeyword.toLowerCase()
				list = list.filter(drug => {
					return (
						(drug.name && drug.name.toLowerCase().includes(keyword)) ||
						(drug.drugCode && drug.drugCode.toLowerCase().includes(keyword)) ||
						(drug.pinyin && drug.pinyin.toLowerCase().includes(keyword))
					)
				})
			}
			
			this.filteredDrugList = list
		},
		
		onCategoryChange(e) {
			this.categoryIndex = e.detail.value
			this.selectedCategory = this.categories[this.categoryIndex]
			if (this.selectedCategory === 'ÂÖ®ÈÉ®ÂàÜÁ±ª') {
				this.selectedCategory = ''
			}
			this.showCategoryPicker = false
			this.filterDrugs()
		},
		
		// ÁÇπÂáªËçØÊùêÂç°ÁâáÔºåÊòæÁ§∫ÊâπÊ¨°ÈÄâÊã©
		async selectDrug(drug) {
			this.selectedDrug = drug
			this.showBatchSelector = true
			this.loading = true
			
			try {
				const result = await wx.cloud.callFunction({
					name: 'stockManage',
					data: {
						action: 'getBatchList',
						data: {
							drugId: drug._id,
							location: 'drug_storage',
							enableFIFO: true
						}
					}
				})
				
				if (result.result.success) {
					this.batchList = result.result.data
				}
			} catch (err) {
				console.error('Âä†ËΩΩÊâπÊ¨°Â§±Ë¥•:', err)
				uni.showToast({ title: 'Âä†ËΩΩÊâπÊ¨°Â§±Ë¥•', icon: 'none' })
			} finally {
				this.loading = false
			}
		},
		
		// ÈÄâÊã©ÊâπÊ¨°ÔºåÂ∏¶ÁùÄËçØÊùêÂíåÊâπÊ¨°‰ø°ÊÅØËøîÂõû
		selectBatch(batch) {
			const drugWithBatch = {
				...this.selectedDrug,
				batch: batch.batch,
				batchId: batch._id,
				expireDate: batch.expireDate,
				productionDate: batch.productionDate,
				stockQuantity: batch.quantity,
				price: batch.price || 0,
				isNearExpiry: batch.isNearExpiry || false
			}
			
			// ‰øùÂ≠òÂà∞ÁºìÂ≠òÔºàÊï∞ÁªÑÊ†ºÂºèÔºå‰øùÊåÅÂÖºÂÆπÔºâ
			uni.setStorageSync('selectedDrugsForOut', [drugWithBatch])
			
			uni.showToast({
				title: 'Â∑≤Ê∑ªÂä†Âà∞Âá∫Â∫ìÂçï',
				icon: 'success',
				duration: 1000
			})
			
			setTimeout(() => {
				uni.navigateBack()
			}, 1000)
		},
		
		closeBatchSelector() {
			this.showBatchSelector = false
			this.selectedDrug = null
			this.batchList = []
		}
	}
}
</script>

